import React, { Component } from "react";
import "./sleepover_info.css";
import sleepover_title from "../resources/images/sleepover.png";

class SleepOver extends Component {
  show_directions() {
    const link =
      "https://maps.google.com/?daddr=Peenjara+Viki";
    window.open(link);
  }

  render() {
    return (
      <div className="sleepover_container">
        <div className="sleepover_title">
          <img src={sleepover_title} className="sleepover_title_photo" />
        </div>
        <div className="sleepover_place_title">Smjetaj:</div>
        <div className="sleepover_place_text"><em>Sobe Viki</em></div>
        <div className="sleepover_price_title">Cijena:</div>
        <div className="sleepover_price_text">90 kn / osoba</div>
        <div className="sleepover_contact_person">Rezervacije:</div>
        <div className="sleepover_contact_number">+385 98 864 888</div>
        <div className="sleepover_directions_plan">
          <button
            className="sleepover_button_style sleepover_button_animated sleepover_button_bounce"
            onClick={this.show_directions}
          >
            Putokaz
          </button>
        </div>
      </div>
    );
  }
}

export default SleepOver;
